<template>
  <div class="showcase" v-hotkey="keymap">
    <div class="game-holder" v-if="game"
         v-bind:class="hideNavBar ? '' : 'full-view-hight-under-navbar'"
         v-bind:style="`background-image: url('api/banner/${game.idUpper}')`" @click="toggleHideNavBar">
      <div class="icon-image-holder">
        <div class="icon-image" v-bind:style="`background-image: url('api/icon/${game.idUpper}')`"></div>
        <div class="game-info">
          <div class="clickable" @click="toggleHideNavBar()">
            <GameModalLink v-bind:game-id="game.id">
              <GameNameHeader v-bind:game="game" />
            </GameModalLink>
          </div>
          <div class="game-players" v-if="game.numberOfPlayers > 0">
            {{game.numberOfPlayers}} {{game.numberOfPlayers === 1 ? 'Player' : 'Players'}}
          </div>
        </div>
      </div>

      <div class="screenshots-holder">
        <div class="screenshot" v-for="i in [0,1,2,3,4,5]" :style="`background-image: url('api/screenshot/${game.idUpper}/${i}')`"></div>
      </div>

      <div class="next-game-preload" v-if="gameNext">
        <div v-bind:style="`background-image: url('api/icon/${gameNext.idUpper}')`"></div>
        <div v-for="i in [0,1,2,3,4,5]" v-bind:style="`background-image: url('api/screenshot/${gameNext.idUpper}/${i}')`"></div>
      </div>
    </div>
  </div>
</template>

<script>
// @ is an alias to /src
import {mapState} from 'vuex'
import {mapMutations} from 'vuex'
import {mapGetters} from 'vuex'
import GameNameHeader from '@/components/GameNameHeader'
import GameModalLink from '@/components/GameModalLink'

export default {
  name: 'showcase',
  components: {GameNameHeader, GameModalLink},
  data() {
    return {
      currentGameIndex: 0,
      myTimeout: null
    }
  },
  created() {
    this.$store.dispatch('fetchGamesData')

    this.resetAndStartTimeout()
  },
  beforeDestroy() {
    clearTimeout(this.myTimeout)
  },
  computed: {
    ...mapState(['showcaseCurrentId', 'hideNavBar', 'showcaseDelay']),
    ...mapGetters(['combinedGames', 'filteredGames']),
    gamesList() {
      return _.shuffle(this.filteredGames, 'hasGame')
    },
    game() {
      if (this.showcaseCurrentId) {
        return _.find(this.combinedGames, {id: this.showcaseCurrentId})
      } else {
        return _.get(this.gamesList, this.currentGameIndex)
      }
    },
    gameNext() {
      if (this.showcaseCurrentId) {
        return null
      } else {
        let index = this.currentGameIndex + 1
        if (index >= _.size(this.gamesList)) {
          index = 0
        }
        return _.get(this.gamesList, index)
      }
    },
    keymap() {
      return {
        // 'esc+ctrl' is OK.
        left: this.rotateGameBack,
        right: this.rotateGameForward
      }
    }
  },
  methods: {
    ...mapMutations(['toggleHideNavBar']),
    resetAndStartTimeout() {
      if (this.myTimeout) {
        clearTimeout(this.myTimeout)
      }
      this.myTimeout = setTimeout(() => {
        this.rotateGame(1)
      }, this.showcaseDelay)
    },
    rotateGameForward() {
      this.rotateGame(1)
    },
    rotateGameBack() {
      this.rotateGame(-1)
    },
    rotateGame(dir = 1) {
      this.currentGameIndex += dir
      if (this.currentGameIndex >= _.size(this.gamesList)) {
        this.currentGameIndex = 0
      } else if (this.currentGameIndex < 0) {
        this.currentGameIndex = _.size(this.gamesList) - 1
      }
      console.log(this.currentGameIndex)
      this.resetAndStartTimeout()
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="scss">
.showcase {
  background: black;
}
.game-holder {
  overflow: hidden;
  background-size: cover;
  position: relative;
  height: 100vh;
}
.icon-image-holder {
  position: absolute;
  left: 5vh;
  top: 5vh;
  background: rgba(0, 0, 0, 0.4);
  border: 1px solid black;

  .icon-image {
    background-size: contain;
    width: 20vh;
    height: 20vh;
    float: left;
  }
  .game-info {
    float: right;
    color: #fff;
    font-size: 3vh;
    text-align: left;
    width: 40vh;
    height: 20vh;
    padding: 5px 5px 5px 20px;

    .game-players {
      margin-top: 20px;
      color: lightgray;
      font-weight: bold;
    }
  }
}

.screenshots-holder {
  position: absolute;
  bottom: 5vh;
  left: 10vh;
  width: 500vw;

  .screenshot {
    height: 9vh * 3;
    width: 16vh * 3;
    background-size: contain;
    float: left;
    margin-right: 2vw;
    border: 2px solid black;
  }
}

.screenshots-holder {
  transform: translate3d(0, 0, 0);
}

.screenshots-holder .screenshot {
  animation: moveSlideshow 25s linear infinite alternate;
}

@keyframes moveSlideshow {
  100% {
    transform: translateX(-300%);
  }
}
</style>
